function sendContact() {
  var valid;
  valid = validateContact();
  alert($('#top-banner-form').serialize());
  if(valid) {
    jQuery.ajax({
      url: "/contact.php",
      data:$('#top-banner-form').serialize(),
      type: "POST",
      success:function(data){
        $("#mail-status").html(data);
        $("#mail-status").css('green');
      },
      error:function (){
        $("#mail-status").html(data);
        $("#mail-status").css('red');
      }
    });
  }
}


